.Toolbar {
  height: 56px;
  width: 100%;
  position: fixed; 
  top: 0;
  left: 0;
  background-color: rgb(240, 190, 198);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 90;
}

.Toolbar nav {
  height: 100%;
}

.Logo {
  margin-bottom: 20px;
  margin-left: 120px; 
  height: 80%;
}

@media (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }
}


